}

.post-filter li.mixitup-control-active {
  color: #464646;
  position: relative;
}

.post-filter li.mixitup-control-active:before {
  position: absolute;
  content: '';
  width: 38px;
  height: 4px;
  bottom: -10px;
  left: 50%;
  margin-left: -19px;
  background-color: #52cbb1;
  background-image: url(data:image/svg+xml;base64,);
  background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, color-stop(0, #19bd9a), color-stop(0.5, #19bd9a), color-stop(0.51, #8ddac9), color-stop(1, #8ddac9));
  /* Android 2.3 */
  background-image: -webkit-repeating-linear-gradient(top, #19bd9a 0%, #19bd9a 50%, #8ddac9 51%, #8ddac9 100%);
  /* IE10+ */
  background-image: repeating-linear-gradient(to bottom, #19bd9a 0%, #19bd9a 50%, #8ddac9 51%, #8ddac9 100%);
  background-image: -ms-repeating-linear-gradient(top, #19bd9a 0%, #19bd9a 50%, #8ddac9 51%, #8ddac9 100%);
}

/* IE8- CSS hack */
@media \0screen\,screen\9 {
  .post-filter li.mixitup-control-active:before {
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ff19bd9a",endColorstr="#ff8ddac9",GradientType=0);
  }
}

